




















export default {
	// props:{
	// 	hasBorder:{
	// 		type:Boolean,
	// 		default:true
	// 	}
	// },
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar
		};
	},
	name: 'cu-custom',
	computed: {
		style() {
			var StatusBar= this.StatusBar;
			var CustomBar= this.CustomBar;
			var bgImage = this.bgImage;
			var Border = this.hasBorder?'1px solid #E5E5E5;':'none;';
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;border-bottom:${Border}`;
			if (this.bgImage) {
				style = `${style}background-image:url(${bgImage});`;
			}
			return style
		}
	},
	props: {
		bgColor: {
			type: String,
			default: 'bg-white'
		},
		isBack: {
			type: [Boolean, String],
			default: false
		},
		bgImage: {
			type: String,
			default: ''
		},
		hasBorder:{
			type:Boolean,
			default:true
		},
		backColor:{
			type:String,
			default:'#333'
		}
	},
	methods: {
		BackPage() {
			uni.navigateBack({
				delta: 1
			});
		}
	}
}
