





































export default{
	name:'cu-tab',
	data() {
		return {
			tabUrl: [
				'/pages/home/home',
				'/pages/friend/friend',
				'/pages/pub/pub',
				'/pages/msg/msg',
				'/pages/my/my'
			]
		}
	},
	props: {
		activeIndex: {
			type: String,
			default: '0'
		},
	},
	computed: {
		nameArr() {
			let arr=[];
			for(let i=1;i<5;i++){
				if(i==this.activeIndex){
					arr.push(`../../static/img/icon/icon_tab${i}_active.png`)
				}else{
					arr.push(`../../static/img/icon/icon_tab${i}.png`)
				}
			}
			return arr;
		}
	},
	methods: {
		handleJump(i) {
			let targetUrl = this.tabUrl[i]
			if(i==2){
				
				uni.navigateTo({
					url:targetUrl
				})
			}else{
				uni.redirectTo({
					url:targetUrl
				})
			}
		}
	},
}
